import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import CardMedia from '@material-ui/core/CardMedia';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';

import ItemPageLoader from './ItemPageLoader';
import itemPageStyles from './itemPageStyles';
import PageNotFound from '../PageNotFound';

class ItemPage extends PureComponent {
  componentDidMount() {
    this.props.getOneItem(this.props.match.params.id);
  }

  render() {
    const { classes, item, addToBasket } = this.props;

    return (
      <div>
        {item.id ? (
          <Paper className={classes.root} elevation={1}>
            <Typography className={classes.model} variant="h5" component="h3">
              {item.model}
            </Typography>
            <CardMedia className={classes.media} image={item.img} />
            <Typography component="p" className={classes.configuration}>
              Configuration: {item.configuration}
            </Typography>
            <Typography component="p">{item.description}</Typography>
            <div className={classes.foter}>
              <Typography component="h3" className={classes.price}>
                Price: {item.price}
              </Typography>
              <Fab
                color="primary"
                aria-label="Add"
                className={classes.fab}
                onClick={addToBasket(item)}
              >
                <AddIcon />
              </Fab>
            </div>
          </Paper>
        ) : (
          <>
            <ItemPageLoader />
            <PageNotFound />
          </>
        )}
      </div>
    );
  }
}

ItemPage.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(itemPageStyles)(ItemPage);
