import React from "react";

import LoadMore from "./LoadMore";
import Item from "../Item/Item";

class ItemList extends React.Component {
  state = {
    limit: 4
  };

  componentDidMount() {
    const { location, history, loadFromServer } = this.props;
    const params = new URLSearchParams(location.search);
    const limit = +params.get("limit");
    if (!limit) {
      history.push("/?limit=4");
    }
    this.setState({ limit: limit || 4 });
    loadFromServer(limit || 4);
  }

  showMore = () => {
    const { history, loadFromServer } = this.props;
    const limit = this.state.limit + 4;
    this.setState({ limit });
    loadFromServer(limit);
    history.push(`/?limit=${limit}`);
  };

  render() {
    const { limit } = this.state;
    const { items, addToBasket } = this.props;
    return (
      <>
        <div className="global">
          {items.map(item => {
            return (
              <Item key={item.id} item={item} addToBasket={addToBasket(item)} />
            );
          })}
        </div>
        {items.length >= limit && (
          <div className="loadButton" onClick={this.showMore}>
            <LoadMore />
          </div>
        )}
      </>
    );
  }
}

export default ItemList;
