import { GET_ITEMS, GET_ONE_ITEM } from './actionTypes';

const getItems = items => ({
  type: GET_ITEMS,
  payload: { items }
});

const getOneItem = item => ({
  type: GET_ONE_ITEM,
  payload: item
});

export function fetchData(limit, page = 1) {
  return dispatch => {
    return fetch(
      `http://localhost:3000/api/items?_page=${page}&_limit=${limit}`
    )
      .then(responce => responce.json())
      .then(data => dispatch(getItems(data)));
  };
}

function handleErrors(response) {
  if (!response.ok) {
    throw Error(response.statusText);
  }
  return response;
}

export function fetchItem(id) {
  return dispatch => {
    return fetch(`http://localhost:3000/api/items/${id}`)
      .then(handleErrors)
      .then(responce => responce.json())
      .then(data => dispatch(getOneItem(data)))
      .catch(function(error) {
        console.log(error);
      });
  };
}
