import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Typography from "@material-ui/core/Typography";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";

import { Link } from "react-router-dom";

const styles = theme => ({
  card: {
    maxWidth: 400,
    margin: 30,
    padding: 7
  },
  fab: {
    margin: theme.spacing.unit
  },
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  actions: {
    display: "flex"
  }
});

class Item extends React.Component {
  render() {
    const { item, addToBasket, classes } = this.props;
    const { model, img, configuration, price, id, description } = item;

    return (
      <Card className={classes.card}>
        <Link to={`/item/${id}`} description={description}>
          <CardMedia className={classes.media} image={img} />
        </Link>
        <CardContent>
          <Typography component="p">{model}</Typography>
        </CardContent>
        <CardActions className={classes.actions} disableActionSpacing>
          <div className="config">{configuration}</div>
          <Fab
            color="primary"
            aria-label="Add"
            className={classes.fab}
            onClick={addToBasket}
          >
            <AddIcon />
          </Fab>
        </CardActions>
        Price: {price}
      </Card>
    );
  }
}

Item.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Item);
