import React, { PureComponent } from "react";
import { Link } from "react-router-dom";
import { withRouter } from "react-router";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import DeleteIcon from "@material-ui/icons/Delete";

import BasketStyle from "./BasketStyle";
import BasketItem from "./BasketItem";

class Basket extends PureComponent {
  state = {
    isSend: false
  };

  send = () => {
    const { clearBasket, history } = this.props;
    clearBasket();
    this.setState({ isSend: true });
    setTimeout(() => {
      history.push("/");
    }, 1000);
  };

  render() {
    const { classes, list, removeFromBasket, clearBasket } = this.props;
    const { isSend } = this.state;
    const totalPrice = list.reduce((sum, cur) => sum + cur.qty * cur.price, 0);

    return (
      <div>
        {list.length ? (
          <>
            {list.map(item => (
              <BasketItem
                item={item}
                key={item.id}
                removeFromBasket={removeFromBasket}
              />
            ))}
            <Paper className={classes.root} elevation={1}>
              <Typography component="p">Total price: {totalPrice}</Typography>
              <Button className={classes.button} onClick={clearBasket}>
                Clear
                <DeleteIcon className={classes.rightIcon} />
              </Button>
              <Button
                variant="contained"
                color="primary"
                className={classes.button}
                onClick={this.send}
              >
                Checkout
              </Button>
            </Paper>
          </>
        ) : (
          <>
            {isSend ? (
              <Paper className={classes.root} elevation={1}>
                Sended
              </Paper>
            ) : (
              <Paper className={classes.root} elevation={1}>
                <Typography component="p">Your basket is Empty</Typography>
                <Link to="/"> Continue shoping</Link>
              </Paper>
            )}
          </>
        )}
      </div>
    );
  }
}

Basket.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withRouter(withStyles(BasketStyle)(Basket));
