import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import CardMedia from "@material-ui/core/CardMedia";
import DeleteIcon from "@material-ui/icons/Delete";
import IconButton from "@material-ui/core/IconButton";

import BasketStyle from "./BasketStyle";

class BasketItem extends PureComponent {
  render() {
    const { classes, removeFromBasket, item } = this.props;
    const { model, price, qty, img, id } = item;

    return (
      <Paper key={id} className={classes.root} elevation={1}>
        <Link to={`/item/${id}`}>
          <Typography component="p">Name: {model}</Typography>
        </Link>
        <Typography component="p">Price: {price}</Typography>
        <Typography component="p">Total: {price * qty}</Typography>
        <Typography component="p">Quantity: {qty}</Typography>
        <CardMedia className={classes.media} image={img} />
        <IconButton className={classes.button} onClick={removeFromBasket(id)}>
          <DeleteIcon />
        </IconButton>
      </Paper>
    );
  }
}

BasketItem.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(BasketStyle)(BasketItem);
