import {
  ADD_TO_BASKET,
  REMOVE_FROM_BASKET,
  CLEAR_BASKET
} from "../actions/actionTypes";

const initialState = {
  list: []
};

export default (state = initialState, action) => {
  switch (action.type) {
    case ADD_TO_BASKET:
      const item = state.list.find(item => item.id === action.item.id);
      if (item) {
        return {
          ...state,
          list: state.list.map(item =>
            item.id === action.item.id ? { ...item, qty: item.qty + 1 } : item
          )
        };
      }
      return {
        ...state,
        list: [...state.list, { ...action.item, qty: 1 }]
      };
    case REMOVE_FROM_BASKET:
      return {
        ...state,
        list: state.list.filter(item => item.id !== action.id)
      };
    case CLEAR_BASKET:
      return {
        ...state,
        list: []
      };
    default:
      return state;
  }
};
